import React from "react";

import { Link, animateScroll as scroll } from "react-scroll";

function Header() {

  const [isBurgerButtonClicked, setIsBurgerButtonClicked] =
    React.useState(false);

    

  function handleOpenMenuBurger() {
    if (isBurgerButtonClicked) {
      setIsBurgerButtonClicked(false);
    }
    if (!isBurgerButtonClicked) {
      setIsBurgerButtonClicked(true);
    }
  }

  const mobileHeader = (
    <>
      <button
        className={`header__burger-menu ${
          isBurgerButtonClicked ? "header__burger-menu_active" : ""
        }`}
        type="button"
        onClick={handleOpenMenuBurger}
      ></button>
      <nav
        className={`header__burger-nav ${
          isBurgerButtonClicked ? "header__burger-nav_active" : ""
        }`}
      >
        <Link
          smooth={true}
          duration={1000}
          className="header__burger-link"
          to="about"
        >
          Главная
        </Link>
        <Link
          smooth={true}
          duration={1000}
          className="header__burger-link"
          to="about-us"
        >
          О нас
        </Link>
        <Link
          smooth={true}
          duration={1000}
          className="header__burger-link"
          to="team"
        >
          Команда
        </Link>
        <Link
          smooth={true}
          duration={1000}
          className="header__burger-link"
          to="education"
        >
          Обучение
        </Link>
        <Link
          smooth={true}
          duration={1000}
          className="header__burger-link"
          to="location"
        >
          Где нас найти?
        </Link>
        <Link
          smooth={true}
          duration={1000}
          className="header__burger-link"
          to="contacts"
        >
          Контакты
        </Link>
      </nav>
    </>
  );

  const header = (
    <nav className="header__nav">
      <Link smooth={true} duration={1000} className="header__link" to="about">
        Главная
      </Link>
      <Link
        smooth={true}
        duration={1000}
        className="header__link"
        to="about-us"
      >
        О нас
      </Link>
      <Link smooth={true} duration={1000} className="header__link" to="team">
        Команда
      </Link>
      <Link
        smooth={true}
        duration={1000}
        className="header__link"
        to="education"
      >
        Обучение
      </Link>
      <Link
        smooth={true}
        duration={1000}
        className="header__link"
        to="location"
      >
        Где нас найти?
      </Link>
      <Link
        smooth={true}
        duration={1000}
        className="header__link"
        to="contacts"
      >
        Контакты
      </Link>
    </nav>
  );

  return (
    <header className="header">
      <div
        className={`header__name-box ${
          isBurgerButtonClicked ? "header__name-box_active" : ""
        }`}
      >
        <h2
          className={`header__name ${
            isBurgerButtonClicked ? "header__name_active" : ""
          }`}
        >
          GENIUS
        </h2>
        <p
          className={`header__subtitle ${
            isBurgerButtonClicked ? "header__subtitle_active" : ""
          }`}
        >
          Лингво-математический центр
        </p>
      </div>
      {mobileHeader}
      {header}
    </header>
  );
}

export default Header;
